export class Solution {
    public divHost: HTMLDivElement;
    public mapboxWrapper: MapboxGLWrapper;
    public renderer: Renderer;
    constructor(id: string) {
        this.divHost = document.getElementById(id) as HTMLDivElement;
        this.divHost.style.position = 'relative';

        this.mapboxWrapper = new MapboxGLWrapper(this.divHost.id);
        this.mapboxWrapper.mouseDown = this.mouseDown
        this.mapboxWrapper.mouseUp = this.mouseUp
        this.mapboxWrapper.mouseClick = this.mouseClick
        this.mapboxWrapper.mouseMove = this.mouseMove

        setTimeout(() => {
            this.renderer = new Renderer(this.divHost, this);

            let boxLayer = new BoxCustomLayer('box')

            this.mapboxWrapper.map.on('load', () => {
                // @ts-ignore
                this.mapboxWrapper.map.addLayer(boxLayer);
            });



            this.mapboxWrapper.map.addLayer({
                id: 'custom-threebox-model',
                type: 'custom',
                renderingMode: '3d',
                onAdd: function () {
                    // Creative Commons License attribution:  Metlife Building model by https://sketchfab.com/NanoRay
                    // https://sketchfab.com/3d-models/metlife-building-32d3a4a1810a4d64abb9547bb661f7f3
                    const scale = 3.2;
                    const options = {
                        obj: 'https://docs.mapbox.com/mapbox-gl-js/assets/metlife-building.gltf',
                        type: 'gltf',
                        scale: { x: scale, y: scale, z: 2.7 },
                        units: 'meters',
                        rotation: { x: 90, y: -90, z: 0 }
                    };

                    // tb.loadObj(options, (model) => {
                    //     model.setCoords([-73.976799, 40.754145]);
                    //     model.setRotation({ x: 0, y: 0, z: 241 });
                    //     tb.add(model);
                    // });
                },

                render: function () {
                    // tb.update();
                }
            });

        }, 200);

    }
    public mouseDown = (e) => {
        // TODO
    }
    public mouseUp = (e) => {
        // TODO
    }
    public mouseDrag = (e) => {
        // TODO
    }
    public mouseClick = (e) => {
        // TODO
        // console.log('dddd');
        // console.log(e);
        // console.log(e['lngLat']);
        this.renderer.pts.push([e['lngLat']['lng'], e['lngLat']['lat']])
    }
    public mouseMove = (e) => {
        // TODO
    }
    public keyDown(k: KeyboardEvent) {
        // TODO
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}
export class Renderer extends RendererCanvas {
    public app: Solution
    public time: number = 0.0;
    public pts: number[][] = [];
    constructor(div: any, app: Solution) {
        super(div);
        this.app = app;
        this.canvas.id = 'mapboxCustom'
        this.canvas.style.position = 'absolute';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';
        this.canvas.style.pointerEvents = 'none';

        // TODO
        // You code goes here for one time operation
        this.pts.push([-71.093161, 42.358871]);

        this.start();
    }
    public mouseDown = (x: number, y: number): void => {

    }
    public mouseUp = (x: number, y: number): void => {

    }
    public mouseDrag = (x: number, y: number): void => {

    }
    public render(ctx: CanvasRenderingContext2D): void {
        this.time += 0.1;

        for (let i = 0; i < this.pts.length; ++i) {
            const pt = this.pts[i];
            const p = this.app.mapboxWrapper.toScreen(pt[0], pt[1]);
            // const p = this.app.mapboxWrapper.map.unproject({x: 10, y: 10})

            ctx.beginPath();
            ctx.arc(p[0], p[1], Math.sin(this.time) * 3 + 15, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fillStyle = '#0f0';
            ctx.fill();
        }

    }
}


class BoxCustomLayer {
    type = 'custom';
    renderingMode = '3d';
    id;

    camera;
    center;
    cameraTransform;
    map;
    scene;
    ball

    controls;
    renderer;

    constructor(id) {
        this.id = id;
    }

    async onAdd(map, gl) {
        this.camera = new THREE.PerspectiveCamera(28, window.innerWidth / window.innerHeight, 0.1, 1e6);

        const { MercatorCoordinate } = mapboxgl;

        const centerLngLat = map.getCenter();
        this.center = MercatorCoordinate.fromLngLat(centerLngLat, 0);
        const { x, y, z } = this.center;

        // console.log(x, y, z)

        const s = this.center.meterInMercatorCoordinateUnits();

        const scale = new THREE.Matrix4().makeScale(s, s, -s);
        const rotation = new THREE.Matrix4().multiplyMatrices(
            new THREE.Matrix4().makeRotationX(-0.5 * Math.PI),
            new THREE.Matrix4().makeRotationY(Math.PI));

        this.cameraTransform = new THREE.Matrix4().multiplyMatrices(scale, rotation).setPosition(x, y, z);

        this.map = map;

        this.scene = this.makeScene();

        // this.scene.add(new THREE.AxesHelper(1000));



        // parameters to ensure the model is georeferenced correctly on the map
        var modelOrigin = [126.735901, 37.348980];
        var modelAltitude = 0;
        var modelRotate = [Math.PI / 2, 0, 0];

        // @ts-ignore
        var modelAsMercatorCoordinate = mapboxgl.MercatorCoordinate.fromLngLat(modelOrigin, modelAltitude);
        // transformation parameters to position, rotate and scale the 3D model onto the map
        var modelTransform = {
            translateX: modelAsMercatorCoordinate.x,
            translateY: modelAsMercatorCoordinate.y,
            translateZ: modelAsMercatorCoordinate.z,
            rotateX: modelRotate[0],
            rotateY: modelRotate[1],
            rotateZ: modelRotate[2],
            /* Since our 3D model is in real world meters, a scale transform needs to be
             * applied since the CustomLayerInterface expects units in MercatorCoordinates.
             */
            scale: modelAsMercatorCoordinate.meterInMercatorCoordinateUnits()
        };

        console.log(modelTransform)





        this.ball = new THREE.Mesh(new THREE.SphereGeometry(100, 8, 8), new THREE.MeshBasicMaterial({ color: 'red' }));
        this.ball.position.set(0, 0, 0);
        this.scene.add(this.ball);


        // use the Mapbox GL JS map canvas for three.js
        this.renderer = new THREE.WebGLRenderer({
            canvas: map.getCanvas(),
            context: gl,
            antialias: true,
        });

        this.renderer.autoClear = false;

        // this.controls = new TransformControls(this.camera, this.renderer.domElement);
        // this.controls.size = 50;
        // this.scene.add(this.controls);
        // this.controls.attach(this.ball);

    }

    makeScene() {
        const scene = new THREE.Scene();
        const skyColor = 0xb1e1ff; // light blue
        const groundColor = 0xb97a20; // brownish orange

        scene.add(new THREE.AmbientLight(0xffffff, 0.25));
        scene.add(new THREE.HemisphereLight(skyColor, groundColor, 0.25));

        const directionalLight = new THREE.DirectionalLight(0xffffff, 0.5);
        directionalLight.position.set(-70, -70, 100).normalize();
        scene.add(directionalLight);

        return scene;
    }

    render(gl, matrix) {
        this.camera.projectionMatrix = new THREE.Matrix4()
            .fromArray(matrix)
            .multiply(this.cameraTransform);
        this.renderer.state.reset();
        this.renderer.render(this.scene, this.camera);
        this.map.triggerRepaint();
    }
}
